const handleButton = async (id: number) => {
  const button = document.getElementById(id.toString()) as HTMLButtonElement

  if (button.textContent == "Add to library") {
    const response = await fetch(`https://api.spaceflightnewsapi.net/v3/articles/${id}`);
    const data = await response.json()

    localStorage.setItem(id.toString(), data.toString())

    button.innerText = "Remove from library"
  } else {
    localStorage.removeItem(id.toString())

    button.innerText = "Add to library"
  }
  console.log(localStorage)
}

export default handleButton
